import React from 'react';
import Layout from '../../../components/layout';
import SEO from '../../../components/seo';
import Box from '../../../components/cardbox';

const seo = {
  title: 'Compounding over time',
  description: '',
};

const roundValue = (val, places = 2) => {
  const num = 10 ** places;
  return Math.round(val * num) / num;
};

class StocksInvestPage extends React.Component {
  constructor() {
    super();
    this.commaRegex = /,/g;
    this.state = {
      monthlyInput: 100,
      averageYield: 5.00,
    };
  }

  changeMonthlyInput = (e) => {
    let val = e.target.value;
    val = val.replace(this.commaRegex, '.');
    this.setState({ monthlyInput: val });
  }

  changeAverageYield = (e) => {
    let val = e.target.value;
    val = val.replace(this.commaRegex, '.');
    this.setState({ averageYield: val });
  }

  computeYear = (arr) => {
    const { averageYield, monthlyInput } = this.state;
    const av = (+averageYield + 100) / 100;
    const lastYear = arr[arr.length - 1];
    const myNextInput = monthlyInput * 12;
    const nextBalance = (lastYear.balance + myNextInput) * av;
    const nextMyTotalInput = lastYear.myTotalInput + myNextInput;
    const nextGrow = nextBalance - nextMyTotalInput;
    arr.push({
      balance: roundValue(nextBalance),
      myTotalInput: roundValue(nextMyTotalInput),
      grow: roundValue(nextGrow),
    });
    return arr;
  }

  computeYield = () => {
    const { averageYield, monthlyInput } = this.state;
    const av = (+averageYield + 100) / 100;
    let arr = [{
      balance: roundValue(monthlyInput * 12 * av),
      myTotalInput: roundValue(monthlyInput * 12),
      grow: roundValue(monthlyInput * 12 * av - monthlyInput * 12),
    }];

    for (let i = 1; i < 30; i += 1) {
      arr = this.computeYear(arr);
    }

    return arr;
  }

  percentFrom = (smallerValue, biggerValue) => roundValue(smallerValue * 100 / biggerValue);

  render() {
    const { averageYield, monthlyInput } = this.state;

    return (
      <Layout>
        <SEO
          {...seo}
        />
        <Box>
          <div className="topbottom_margined">
            Average yield in %
            <input className="margin-left" onChange={this.changeAverageYield} value={averageYield} />
          </div>
          <div>
            Monthly contribution
            {' '}
            <input className="margin-left" onChange={this.changeMonthlyInput} value={monthlyInput} />
          </div>
          <table>
            <thead>
              <tr>
                <th>Year</th>
                <th>Value</th>
                <th>My total contribution</th>
                <th>Total Grow</th>
              </tr>
            </thead>
            {this.computeYield().map((obj, i) => (
              <tr>
                <td>
                  {i}
                  {' '}
                  Year
                </td>
                <td>
                  {obj.balance}
                  {' '}
                  €
                </td>
                <td>
                  {obj.myTotalInput}
                  {' '}
                  €
                  {' '}
                  <em>
                    (
                    {this.percentFrom(obj.myTotalInput, obj.balance)}
                    {' '}
                    %)
                  </em>
                </td>
                <td>
                  {obj.grow}
                  {' '}
                  €
                  {' '}
                  <em>
                    (
                    {this.percentFrom(obj.grow, obj.balance)}
                    {' '}
                    %)
                  </em>
                </td>
              </tr>
            ))}
          </table>
        </Box>
      </Layout>
    );
  }
}

export default StocksInvestPage;
